@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #151c22;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  display:flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  color:white;
}

#display h5 {
  text-align: center;
  /* position: relative; */
  /* left:96vw; */
  /* z-index: 1; */
  /* bottom:-10vh; */
}

.container {
    position: relative;
    width: 300px;
    height:39vh;
    background-color: lighten(grey, 20%);
}


.Pad {
    transition:0.4s;
    position: relative;
    float: left;

    margin-top: 10px ;

    width: 90px;
    height:90px;
    /* background-color: #303131; */
    text-align: center;

    margin-right: 10px;
    border-radius: 5px;
    padding-top: 35px;
    box-sizing: border-box;
    cursor: pointer;
}
.soundBankChange {
    list-style: none;
    
    /* position: relative; */
    /* left:1.7vw; */
}

.soundBankChange li {
    display: inline-block; 

    transition: 0.2s;
}

.soundBankChange ul li {
    font-size:20px;
    margin: 7px;
    cursor:pointer;
}

.soundBankChange li:hover {
    font-size: 26px
}
.refresh-button {
    border-radius: 5px;
    height:4vh;
    width:100vw;
    border: none;
    color:rgb(241, 241, 241);
    background-color: #3d3f41;
    position: absolute;
    bottom:0;
    left:0;
    opacity: 0.8;
    transition: 0.25s;
  }
  
  .refresh-button:hover {
    opacity:1;
  }
.volumeIcon{
    position: absolute;
    bottom:8.5vh;
    left:49vw;
    color:#2b2d2f;
    transition: 0.7s;
    cursor: pointer;
}
