.refresh-button {
    border-radius: 5px;
    height:4vh;
    width:100vw;
    border: none;
    color:rgb(241, 241, 241);
    background-color: #3d3f41;
    position: absolute;
    bottom:0;
    left:0;
    opacity: 0.8;
    transition: 0.25s;
  }
  
  .refresh-button:hover {
    opacity:1;
  }